
import { defineComponent } from 'vue';
import Usuario from '@/Models/Usuario';
import LoginService from '@/Services/LoginService';

export default defineComponent({
    name: 'LoginView',

    data(): {
        service: LoginService,
        result: string | undefined,
        usuario: Usuario,
        emailPattern: RegExp,
        senhaPattern: RegExp
    } {
        return {
            service: new LoginService(this.axios),
            result: undefined,
            usuario: new Usuario(),
            emailPattern: /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/,
            senhaPattern: /.{4,}/
        }
    },

    emits: ['goToPage'],

    methods: {
        goToPage(page: string) {
            this.$emit('goToPage', page);
        },
        formValido() {
            if (!this.usuario.email || !this.usuario.senha) {
                return false;
            }

            let emailValido = this.emailPattern.test(this.usuario.email);
            let senhaValida = this.senhaPattern.test(this.usuario.senha);

            return emailValido && senhaValida;
        },
        async signIn() {
            
            if(!this.formValido()){
                this.result = "Os dados informados so invlidos.";
                return;
            }

            try {
                this.result = undefined;
                let access_token = await this.service.login(this.usuario);
                this.usuario = new Usuario();
                localStorage.setItem('access_token', access_token);
                this.goToPage('Menu');
            }
            catch (error: any) {
                this.result = error ?? 'Houve uma falha no servidor.';
            }
        },

        acessoLocal() {
            localStorage.setItem('access_token', 'local_access');
            this.goToPage('Menu');
        },
        clearResult() {
            if(this.result)
                this.result = undefined;
        }
    }
})
