
import { defineComponent } from 'vue';

export default defineComponent({
    name: "HeaderComponent",

    data(): {
        back: boolean,
        backTo: string,
        icon: string,
        title: string,
        login: boolean,
        sair: boolean
    } {
        return {
            back: false,
            backTo: '',
            icon: '',
            title: '',
            login: false,
            sair: false,
        }
    },

    props: {
        page: String,
        hideBackButton: Boolean
    },

    emits: ['goToPage'],

    methods: {
        goToPage(page: string) {
            this.$emit('goToPage', page);
        },
        doSair() {
            localStorage.removeItem('access_token');
            this.goToPage('Home');
        },
        definirHeader() {
            let page = localStorage.getItem('page');

            if (page == 'Home') {
                this.back = false;
                this.backTo = '';
                this.icon = 'pi pi-table';
                this.title = 'Grade Escolar';
                this.login = true;
                this.sair = false;
            } else if (page == 'Login') {
                this.back = true;
                this.backTo = 'Home';
                this.icon = 'pi pi-sign-in';
                this.title = 'Entrar';
                this.login = false;
                this.sair = false;
            } else if (page == 'Cadastro') {
                this.back = true;
                this.backTo = 'Login';
                this.icon = 'pi pi-id-card';
                this.title = 'Cadastro';
                this.login = false;
                this.sair = false;
            } else if (page == 'Menu') {
                this.back = false;
                this.backTo = '';
                this.icon = 'pi pi-table';
                this.title = 'Grade Escolar';
                this.login = false;
                this.sair = true;
            } else if (page == 'Aula') {
                this.back = !this.hideBackButton;
                this.backTo = 'Menu';
                this.icon = 'pi pi-file-edit';
                this.title = 'Aulas';
                this.login = false;
                this.sair = false;
            } else if (page == 'Anotacoes') {
                this.back = !this.hideBackButton;
                this.backTo = 'Menu';
                this.icon = 'pi pi-book';
                this.title = 'Anotaes';
                this.login = false;
                this.sair = false;
            } else if (page == 'DisciplinaConfig') {
                this.back = true;
                this.backTo = 'Menu';
                this.icon = 'pi pi-cog';
                this.title = 'Disciplinas';
                this.login = false;
                this.sair = false;
            } else if (page == 'GradeConfig') {
                this.back = true;
                this.backTo = 'Menu';
                this.icon = 'pi pi-cog';
                this.title = 'Grade';
                this.login = false;
                this.sair = false;
            } else if (page == 'AulaConfig') {
                this.back = true;
                this.backTo = 'Menu';
                this.icon = 'pi pi-cog';
                this.title = 'Aulas';
                this.login = false;
                this.sair = false;
            }
        }
    },

    mounted() {
        this.definirHeader();
    },

    watch: {
        page() {
            this.definirHeader();
        },
        hideBackButton() {
            this.definirHeader();
        }
    }
})
