
import Grade from '@/Models/Grade';
import Dia from '@/Models/Dia';
import { defineComponent } from 'vue';
import AuthService from '@/Services/AuthService';
import GradeService from '@/Services/GradeService';

export default defineComponent({
    name: "GradeConfigView",

    data(): {
        service: GradeService,
        hasData: boolean,
        grade: Grade | undefined,
        aulas: number,
        dias: Dia[],
        result: string | undefined
    } {
        return {
            service: new GradeService(this.axios),
            hasData: false,
            grade: undefined,
            aulas: 0,
            dias: Dia.dias(),
            result: undefined
        }
    },
    emits: ['goToPage'],

    methods: {
        goToPage(page: string) {
            this.$emit('goToPage', page);
        },
        lerGrade() {
            this.dias = Dia.montarTodos(this.grade!.dias);
            this.aulas = this.grade!.aulas
        },
        async persistirGrade() {
            this.grade!.dias = Dia.desmontar(this.dias);
            this.grade!.aulas = this.aulas;
            await this.service.atualizar(this.grade!);
            this.result = "Grade salva!";
        },
        async obterGrade() {
            try {
                this.grade = await this.service.obter();    
                this.lerGrade();
                this.hasData = true;
            } catch (error) {
                this.grade = undefined;
            }            
        },
        validar(dia: Dia) {
            this.clearResult();
            if(this.dias.find(d => d.ativo) == undefined) {
                dia.ativo = true;
            }
        },
        clearResult() {
            if(this.result)
                this.result = undefined;
        }
    },

    async mounted() {
        if (!AuthService.autenticado){
            this.goToPage('Home');
            return;
        }


        await this.obterGrade();
    }
})
