
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'MsgBoxComponent',

    props: {
        titulo: String,
        mensagem: String,
        icone: String,
        yes: Boolean,
        no: Boolean,
        ok: Boolean,
        cancel: Boolean
    },

    data(): {} { return {} },

    emits: ['msgboxResult'],

    methods: {
        msgboxResult(result: boolean) {
            this.$emit('msgboxResult', result);
        },
        nothing() {

        }
    },

    mounted() { }
});
