
import AuthService from '@/Services/AuthService';
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'MenuView',

    data(): {
    } {
        return {
        };
    },

    emits: ['goToPage'],

    methods: {
        goToPage(page: string) {
            this.$emit('goToPage', page);
        }
    },

    mounted() {
        if (!AuthService.autenticado) {
            this.goToPage('Home');
        }
    }

});
